@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");



.logo {
  margin-top: 5px;
  height: 50px;
  width: 54px;
  margin-left: 1%;
  margin-right: -8%;
  cursor: pointer;
}


.left {
  align-items: left;
}

.right1 {
  align-items: right;
  margin-left: 50%;
}


.atag {
  color: white;
  margin-right: 0px;
  font-size: 12px;
}

.right2 {
  align-items: right;
  margin-right: 20px;
}

@media (max-width: 1250px) {
  .hidden-mobile {
    display: none;
  }
}

.logo1 {
  margin-top: 12px;
  height: 20px;
  width: 24px;
  cursor: pointer;
  border: solid;
  padding: 10px;
  border-color: #c6c3c3;
  border-radius: 100%;
}

.logo2 {
  margin-top: 24px;
  height: 20px;
  width: 24px;
  cursor: pointer;
}

.navv {
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
  max-width: 100%;
  margin: 0 auto;
  max-height: 72px;
  background-color: #2e2e2e;
}

.navv ul {
  display: flex;
  list-style: none;
  gap: 24px;
}
.navv ul a {
  color: #f9f7f7;
}

.site-settings {
  font-size: 14px;
  text-decoration: none;
  margin: 16px;
  padding: 5px;
  color: #f9f7f7;
}

.hero-btn {
  display: flex;
  gap: 40px;
}

.menu-toggle {
  cursor: pointer;
  padding: 10px;
  align-self: center;
  margin-bottom: 20px;
  margin-left: -90%;
  all: unset;
  padding: 6px 16px;
  background: var(--red);
  height: 50px;
  color: white;
  font-weight: 500;
}

.bar {
  width: 30px;
  height: 3px;
  background-color: #faf9f9;
  margin: 6px 0;
}

@media screen and (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  .nav-menu {
    display: none;
  }

  .nav-menu.active {
    display: block;
  }
}
.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
  color: #2e2c34;
}

.sidenav a:hover {
  color: #fff;
  background-color: #2e2c34;
}

.sidenav {
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  position: fixed;
  top: 15%;
  left: 2.5%;
  overflow-x: hidden;


}
.heading2 {
  color: #2e2c34;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
  position: fixed;
  top: 10%;
  left: 2.5%
}
